.App {
  text-align: center;
  background: linear-gradient(to bottom, #282c34, transparent, #282c34),
    radial-gradient(circle, transparent 0%, #282c34 100%);
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-attachment: fixed;
  min-height: 100vh;
}

.App::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  filter: blur(100px);
  background: linear-gradient(95deg, #4873aa, #3578cf, #0b2c56, #4e70d0);
  background-size: 200% 200%;
  animation: animateGlow 15s linear infinite;
  text-align: center;
}

@media screen and (max-width: 600px) {
  .App {
    text-align: center;
    background: linear-gradient(to bottom, #282c34, transparent, #282c34),
      radial-gradient(circle, transparent 0%, #282c34 100%);
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    background-attachment: fixed;
    min-height: 100vh;
  }
}

@media screen and (max-width: 600px) {
  .App::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    filter: blur(30px);
    background: linear-gradient(95deg, #4873aa, #3578cf, #0b2c56, #4e70d0);
    background-size: 200% 200%;
    animation: animateGlow 15s linear infinite;
    text-align: center;
  }
}

@media (max-width: 768px) {
  .navbar-list {
    flex-direction: column;
  }

  .navbar-item {
    margin-right: 0;
    margin-bottom: 10px;
  }

  button {
    font-size: 14px;
  }
}

@keyframes animateGlow {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}

